*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}

input{
  font-size: 1.2rem;
  width: 100%;
}
.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-top: 10px; 
}

.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 150px;
  
}
.card-grid{
  display: flex;
  flex-direction:row ;
  flex-wrap: wrap;
}